import { AxiosRequestConfig } from "@/services/types";
import { $api } from "@/services/constant";
import { HOST_API_V1 } from "@/services/env-init";

const BASE_URL = HOST_API_V1 + "public/";

export const forgotPassword = async (payload: any) => {
  const res = await $api().put(BASE_URL + "forgot-password", payload);
  return res.data;
};

export const updateProfile = async (payload: any) => {
  const res = await $api().put(BASE_URL + "profile", payload);
  return res.data;
};

export const resetPassword = async (payload: any) => {
  const res = await $api().put(BASE_URL + "reset-password", payload);
  return res.data;
};

export const resetPin = async (payload: any) => {
  const res = await $api().put(BASE_URL + "reset-pin", payload);
  return res.data;
};

export const getPackage = async () => {
  const res = await $api().get(BASE_URL + "package");
  return res.data;
};

export const getPayment = async () => {
  const res = await $api().get(BASE_URL + "payment");
  return res.data;
};

export const getPromo = async (params: AxiosRequestConfig) => {
  const res = await $api().get(BASE_URL + "promo", params);
  return res.data;
};
